* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Poppins, sans-serif;
}

.navbar {
  width: 100%;
  height: 70px;
  box-shadow: 0px 2.98px 7.46px 0px #0000001a;
  display: flex;
  align-items: center;
}

.navbar__logo {
  padding-left: 32px;
  height: 26px;
}

.hero__img {
  width: 100%;
  height: 100%;
}
.hero {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 100px;

  h2 {
    font-weight: 600;
    font-size: 36px;
    line-height: 40px;
    padding-top: 32px;
    width: 100%;
  }

  p {
    padding-top: 16px;
    font-size: 1rem;
    width: 35%;
    align-self: start;
    line-height: 17.6px;
  }

  h2,
  p {
    text-align: left;
  }
}

.container {
  width: 100%;
  height: 100%;
  padding: 100px;
  display: grid;
  grid-template-columns: auto auto auto;
}

.card {
  width: 180px;
  height: 100%;
}

.card__photo {
  width: 100%;
  object-fit: contain;
}

.card__check {
  background-color: #fff;
  color: #0f0f0f;
  text-align: center;
  width: 64px;
  font-size: 11px;
  position: relative;
  bottom: -24px;
  left: 6px;
}

.card__rating {
  display: flex;

  p {
    font-size: 14px;
    color: #555;
    letter-spacing: 5%;

    span {
      color: #000;
      font-weight: 500;
    }
  }
}

.card__star {
  width: 12px;
  align-self: center;
  margin-right: 2px;
}

.card__title {
  font-size: 12px;
  padding: 3px 0;
}

.card__price {
  font-size: 12px;

  span {
    font-weight: 600;
  }
}
